.ViewPort {
/*	border: 5px solid red;*/
	margin: auto;
	height: 600px;
	width: 900px;
	overflow: hidden;
	position: relative;
	/* height: 1240px;
	width: 1750px; */
}
.WorldMap {
/*	background-image: url('../public/assets/001.jpg');*/
/*	background-size: contain;*/
	height: 1240px;
	position: absolute;
	transition: top 100ms, left 100ms;
	width: 1750px;
}

.WorldMapData {
	top: 0;
	left: 0;
	height: 1240px;
	position: absolute;
	transition: top 100ms, left 100ms;
	width: 1750px;
}

.WorldMap .row {
	display: flex;
	flex-direction: row;
}

.WorldMap .row .cell {
/*	background-color: #bbb;*/
	border: 1px solid #ddd;
	height: 8px;
	width: 8px;
}

.WorldMap .row .wall {
	background-color: rgba(255, 0, 0, 0.3);
}

.Player {
	background-image: url('../public/assets/player.jpg');
	background-size: contain;
	height: 20px;
	width: 20px;
	position: absolute;
	transition: top 100ms, left 100ms;
}