.App {
  text-align: center;
  padding-top: 100px;
}

.loader {
  border: 6px solid #3498db;
  width: 10px;
  height: 10px;
  animation: spin 3s linear infinite;
  position: fixed;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}